@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    background-color: rgb(68, 5, 185);
}
.container{
    background-color: rgb(68, 5, 185);
    display: flex;
    flex-direction: column;
    font-family: 'Montserrat', sans-serif;
}
#h1{
    margin: 10px;
    color: white;
    background-color: rgb(68, 5, 185);
    font-size: 30px;
}
.header{
    margin: 10px;
    color: black;
    font-size: 30px;
}
#p2{
    font-weight: bold;
    margin: 10px;
    color: rgb(75, 75, 75);
    font-size: 18px;
}
.btncls{
    width:250px;
    cursor: pointer;
    padding: 10px;
    border-radius: 20px;
    margin: 15px;
    background-color: rgb(114, 214, 68);
    color: white;
    font-size: 10px;
    border: none;
    outline: none;
} 
.options{
    display: grid;
    grid-template-columns: auto auto;
    grid-gap:80px;
    padding: 80px;
}
.options > button{
    width:100%;

    padding: 10px;
    border-radius: 15px;
    margin:20px;
    cursor: pointer;
    margin-right: 70px;
    background-color: rgb(41, 192, 252);
    color: white;
    font-size: 15px;
    border: none;
    outline: none;
}
.content{
    justify-self: center;
    margin: 70px;
}
.quiz-content{
    margin: 70px;
    justify-self: center;
    background-color: white;
    padding: 30px;
}
.button-items{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.button-items >button{
    margin: 10px;
    border: 2px solid rgb(156, 156, 156);
    height: 40px;
    padding: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    cursor: pointer;
    color: white;
}
#prev{
    background-color: rgb(2, 161, 224);
}
#nxt{
    background-color: rgb(141, 235, 97);
}
#qt{
    background-color: rgb(248, 78, 120);
}
.result{
    color: rgb(114, 214, 68);
}
.result-content{
    background-color: white;
    margin-left: 270px;
    margin-right: 270px;
    padding: 30px;
}
#h2{
    padding: 20px;
    color:rgb(75, 75, 75);
}
#score{
    padding: 20px;
    color: rgb(2, 161, 224);
}
#stats{
    color:rgb(114, 114, 114);
    font-weight: 800;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
#home{
    color:rgb(114, 214, 68);
    border: solid 2px rgb(114, 214, 68);
    background-color: rgb(68, 5, 185);
    padding: 5px;
    height: 30px;
    padding-left: 25px;
    padding-right: 25px;
}
#play{
    color:rgb(2, 161, 224);
    border: solid 2px rgb(2, 161, 224);
    background-color: rgb(68, 5, 185);
    padding: 5px;
    height: 30px;
    padding-left: 25px;
    padding-right: 25px;  
}
.container i{
    color: rgb(114, 214, 68);
    font-size: 50px;
    font-weight: 500;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

